.header{
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: var(--z-fixed);
    background-color: var(--body-color);
}

.nav{
    height: calc(var(--header-hieght) + 1.5rem);
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 1rem;
}

.nav__logo{
  font-size: var(--h2-font-size);
  /* font-weight: var(--font-semi-bold); */
  font-weight: bolder !important;
}

.nav__logo,
.nav__toggle{
    color: var(--title-color);
    font-weight: var(--font-medium);
}

:is(.nav__logo,
.nav__toggle):hover{
  color: var(--secondary-color);
}

.nav__list{
    display: flex;
    column-gap: 2rem;
}

.nav__link{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
    transition: .3s;

}

.nav__icon,
.nav__close,
.nav__toggle{
    display: none;
}

.active-link,
.nav__link:hover{
    /* color: var(--title-color-dark); */

    color: rgb(74, 110, 255);
}

/* Breakpoints */

/* For medium devices */
@media screen and (max-width:768px) {
  .header{
    top: initial;
    bottom: 0;
  }

  .nav{
    height: var(--header-hieght);
  }

  .nav__menu{
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: .3s;
  }

  /* Show Menu */
  .show-menu{
    bottom: 0;
  }

  .nav__list{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }

  .nav__icon{
    font-size: 1.2rem;
  }

  .nav__close{
    position: absolute;
    right: 1.3rem;
    bottom: .5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--title-color);
  }
  
  .nav__close:hover{
    /* color: var(--title-color-dark); */

    color: rgb(74, 110, 255);
  }

  .nav__toggle{
    font-size: 1.1rem;
    cursor: pointer;
  }

  .nav__icon,
  .nav__close,
  .nav__toggle{
     display: block;
  }
}

/* For small devices */
@media screen and (max-width:350px) {
  .nav__menu{
    padding: 2rem 0.25rem 4rem;
  }

  .nav__list{
    column-gap: 0;
  }
}